<template lang="pug">
include ../../../mixins/bemto/bemto
+b.neuro-input-text(@click="clickHandler", :class="mainModifiers")
  .input-wrapper
    +e.input
      component(
        :is="properties.inputType",
        :type="properties.type",
        :value.prop="properties.value",
        :placeholder="properties.placeholder",
        :disabled="properties.disabled",
        @input="inputHandler",
      )
</template>

<script>
import propertiesHandler from '@/mixins/propertiesHandler'
export default {
  name: 'NeuroInputText',
  mixins: [propertiesHandler],
  computed: {
    mainModifiers() {
      const arr = []
      this.properties.modifiers.forEach(mod => {
        arr.push(`neuro-input-text--${mod}`)
      })
      return arr
    },
  },
  props: {
    value: {
      type: [String, Number],
      default() {
        return ''
      },
    },
    label: {
      type: String,
      default() {
        return ''
      },
    },
    placeholder: {
      type: String,
      default() {
        return ''
      },
    },
    inputType: {
      type: String,
      default() {
        return 'input'
      },
    },
    type: {
      type: String,
      default() {
        return 'text'
      },
    },
    modifiers: {
      type: Array,
      default() {
        return []
      },
    },
    disabled: {
      type: Boolean,
      default() {
        return false
      },
    },
  },
  methods: {
    clickHandler() {
      this.$emit('click-handler')
    },
    inputHandler(event) {
      const model = this.properties.modelIdent
      this.$emit('input-handler', event.target.value, model)
    },
  },
}
</script>
